import React from 'react';
import styled from '@emotion/styled';
import logo from '../../static/logo/banner.png';
import PropTypes from 'prop-types';
import { StaticQuery, graphql } from 'gatsby';

const Wrapper = styled.footer`
	position: relative;
	bottom: 0;
	box-shadow: ${props => props.theme.shadow.footer};
	background: ${props => props.theme.colors.primary.base};
	font-family: ${props => props.theme.fontFamily.body};
	font-weight: 500;
`;

const ElementContainer = styled.div`
	display: flex;
	padding: 1rem;
	width: 100%;
	min-height: 12.5rem;
	max-width: ${props => props.theme.layout.base};
	color: ${props => props.theme.colors.white.base};
	margin: 0 auto;
	align-items: center;
	justify-content: space-between;
	@media (max-width: ${props => props.theme.breakpoints.s}) {
		flex-direction: column;
	}
`;

const Logo = styled.img`
	flex: 1;
	width: 25%;
	max-width: 25%;
	margin: 0;
	padding-right: 1rem;
	@media (max-width: ${props => props.theme.breakpoints.s}) {
		width: 100%;
		max-width: 100%;
	}
`;

const TextContainer = styled.div`
	flex: 1;
	@media (max-width: ${props => props.theme.breakpoints.s}) {
		width: 100%;
		padding-top: 1rem;
	}
`;

const Text = styled.p`
	font-size: 0.875rem;
	line-height: 0.875rem;
	font-weight: 400;
	&:last-child {
		margin: 0;
	}
`;

const FooterLink = styled.a`
	color: ${props => props.theme.colors.link.light};
	transition: all ${props => props.theme.transitions.default.duration};
	&:hover {
		color: ${props => props.theme.colors.link.hover};
	}
`;

const Footer = () => (
	<StaticQuery
		query={query}
		render={({
			site: {
				siteMetadata: { title },
			},
		}) => (
			<Wrapper>
				<ElementContainer>
					<Logo src={logo} alt={'ExpediaGroup Logo'} />
					<TextContainer>
						<Text>{title}</Text>
						<Text>
							<FooterLink href="https://www.lifeatexpedia.com" target="_blank">
								Careers
							</FooterLink>
						</Text>
						<Text>Â© 2019 Expedia Group. All rights Reserved.</Text>
					</TextContainer>
				</ElementContainer>
			</Wrapper>
		)}
	/>
);
export default Footer;

Footer.propTypes = {
	data: PropTypes.shape({
		site: PropTypes.shape({
			siteMetadata: PropTypes.shape({
				title: PropTypes.string,
			}).isRequired,
		}),
	}),
};

export const query = graphql`
	query {
		site {
			siteMetadata {
				title
			}
		}
	}
`;
