import React, { Fragment } from 'react';
import { ThemeProvider } from 'emotion-theming';
import { css, Global } from '@emotion/core';
import PropTypes from 'prop-types';
import { SEO } from '../components';
import { NavBar, Footer } from './index';
import theme from '../../config/theme';
import headroom from '../styles/headroom';
import fontFiles from '../fonts';

const globalStyles = css`
	@font-face {
		font-family: 'Century Gothic Regular';
		font-style: normal;
		font-weight: 400;
		src: local('Century Gothic Regular'), local('CenturyGothicRegular'),
			url(${fontFiles.regular}) format('woff');
	}
	@font-face {
		font-family: 'Century Gothic Bold';
		font-style: normal;
		font-weight: 400;
		src: local('Century Gothic Bold'), local('CenturyGothicBold'),
			url(${fontFiles.bold}) format('woff');
	}
	*,
	*:before,
	*:after {
		box-sizing: inherit;
	}
	html {
		text-rendering: optimizeLegibility;
		overflow-x: hidden;
		box-sizing: border-box;
		-ms-overflow-style: scrollbar;
		-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
	}
	html,
	body {
		width: 100%;
		height: 100%;
		margin: 0;
		padding: 0;
	}

	body {
		display: flex;
		flex-direction: column;
		min-height: 100vh;
	}
	a {
		color: ${theme.colors.link.base};
		transition: color 0.5s;
		text-decoration: none;
	}
	a:hover {
		text-decoration: none;
		color: ${theme.colors.link.hover};
	}
	h1 {
		font-family: ${theme.fontFamily.bold};
	}

	${headroom}
`;

const Layout = ({ children }) => (
	<ThemeProvider theme={theme}>
		<Fragment>
			<Global styles={globalStyles} />
			<SEO />
			<NavBar />
			{children}
			<Footer />
		</Fragment>
	</ThemeProvider>
);

export default Layout;

Layout.propTypes = {
	children: PropTypes.oneOfType([PropTypes.array, PropTypes.node]).isRequired,
};
