/** @jsx jsx */
// eslint-disable-next-line
import React, { useState } from 'react';
import { Link } from 'gatsby';
import styled from '@emotion/styled';
import { css, jsx } from '@emotion/core';
import Headroom from 'react-headroom';
import logo from '../../static/logo/banner.png';

const NavContainer = styled.div`
	display: flex;
	max-width: ${props => props.theme.layout.base};
	width: 100%;
	justify-content: space-between;
	flex-wrap: wrap;
	margin: 0 auto;
`;

const StyledLink = styled(Link)`
	display: flex;
	font-weight: 700;
	align-items: center;
	max-width: 85%;
`;

const Logo = styled.img`
	margin: 0;
	height: 5rem;
	object-fit: contain;
`;

const NavLinksContainer = styled.nav`
	display: flex;
	align-items: center;
	font-family: ${props => props.theme.fontFamily.body};
	font-weight: 500;
	font-size: 1.1rem;
	background: ${props => props.theme.colors.primary.base};
	@media (max-width: 700px) {
		flex-direction: column;
	}
`;

const NavLink = styled.a`
	color: ${props => props.theme.colors.link.light};
	margin-left: 2rem;
	transition: all ${props => props.theme.transitions.default.duration};
	&:hover {
		color: ${props => props.theme.colors.link.hover};
	}
	@media (max-width: 700px) {
		margin-left: 0;
		margin-top: 0.5rem;
		width: 100%;
		display: block;
	}
`;

const openMobileNavStyles = css`
	@media (max-width: 700px) {
		flex-grow: 1;
		flex-basis: 100%;
	}
`;
const closedMobileNavStyles = css`
	@media (max-width: 700px) {
		display: none;
	}
`;

const MobileNavTrigger = styled.button`
	display: flex;
	color: ${props => props.theme.colors.white.base};
	font-size: 2rem;
	cursor: pointer;
	align-items: center;
	background: transparent;
	border: none;
	padding: 0;
	margin-left: 1rem;
	&:hover {
		color: ${props => props.theme.colors.link.hover};
	}
	@media (min-width: 700px) {
		display: none;
	}
`;

const NavBar = () => {
	const StyledNavLink = NavLink.withComponent(Link);
	const [isNavOpen, setIsNavOpen] = useState(false);
	const [navClass, setNavClass] = useState(closedMobileNavStyles);

	const toggleMobileNav = () => {
		setNavClass(isNavOpen ? closedMobileNavStyles : openMobileNavStyles);
		setIsNavOpen(!isNavOpen);
	};
	return (
		<Headroom calcHeightOnResize>
			<NavContainer>
				<StyledLink to="/">
					<Logo src={logo} alt="ExpediaGroup Open Source Logo" />
				</StyledLink>
				<MobileNavTrigger onClick={toggleMobileNav}>
					{/* mobile menu icon */}
					&#9776;
				</MobileNavTrigger>
				<NavLinksContainer css={navClass}>
					<StyledNavLink to="/">Home</StyledNavLink>
					<StyledNavLink to="/projects">Projects</StyledNavLink>
					<NavLink
						href="https://medium.com/expedia-group-tech"
						target="_blank"
						rel="noopener noreferrer"
					>
						Tech Blog
					</NavLink>
				</NavLinksContainer>
			</NavContainer>
		</Headroom>
	);
};
export default NavBar;
