import React from 'react';
import { Link, graphql } from 'gatsby';
import Img from 'gatsby-image';
import styled from '@emotion/styled';
import PropTypes from 'prop-types';
import theme from '../../config/theme';

const columnsByViewportSize = {
	xl: 4,
	lg: 3,
	md: 2,
};
const Wrapper = styled.article`
	margin: 0 0.75rem 2rem 0.75rem;
	position: relative;
	z-index: 100;
	border-radius: ${props => props.theme.borderRadius.default};
	box-shadow: ${props => props.theme.shadow.feature.small.default};
	transition: ${props => props.theme.transitions.boom.transition};
	height: 17rem;
	flex-basis: calc(99.9% * 1 / ${columnsByViewportSize.xl} - 1.5rem);
	max-width: calc(99.9% * 1 / ${columnsByViewportSize.xl} - 1.5rem);
	width: calc(99.9% * 1 / ${columnsByViewportSize.xl} - 1.5rem);

	&:hover {
		box-shadow: ${props => props.theme.shadow.feature.small.hover};
		transform: scale(1.04);
	}

	@media (max-width: 1300px) {
		flex-basis: calc(99.9% * 1 / ${columnsByViewportSize.lg} - 1.5rem);
		max-width: calc(99.9% * 1 / ${columnsByViewportSize.lg} - 1.5rem);
		width: calc(99.9% * 1 / ${columnsByViewportSize.lg} - 1.5rem);
		height: 18rem;
	}

	@media (max-width: 1000px) {
		flex-basis: calc(99.9% * 1 / ${columnsByViewportSize.md} - 1.5rem);
		max-width: calc(99.9% * 1 / ${columnsByViewportSize.md} - 1.5rem);
		width: calc(99.9% * 1 / ${columnsByViewportSize.md} - 1.5rem);
		height: 18rem;
	}

	@media (max-width: 700px) {
		flex-basis: 100%;
		max-width: 100%;
		width: 100%;
		height: 15rem;
	}
`;

const StyledLink = styled(Link)`
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	padding: 1rem;
	z-index: 3;
	border-radius: ${props => props.theme.borderRadius.default};
	&:after {
		content: '';
		position: absolute;
		display: block;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: rgba(0, 50, 153, 0.6) 100%;
		z-index: -10;
		border-radius: ${theme.borderRadius.default};
		transition: opacity ${theme.transitions.default.duration};
	}
`;

const Info = styled.div`
	color: ${props => props.theme.colors.white.light};
	margin: 0 1rem 1.25rem 1.25rem;
	position: absolute;
	bottom: 0;
	left: 0;
`;

const Title = styled.h2`
	margin-bottom: 0.6rem;
`;

const Excerpt = styled.p`
	font-family: ${props => props.theme.fontFamily.bold};
	margin-bottom: 0.5rem;
`;

const imageStyles = props => ({
	objectFit: props.containImgWidth ? 'contain' : 'cover',
	borderRadius: props.theme.borderRadius.default,
	height: '100%',
});

const PostListItem = ({
	cover,
	path,
	date,
	title,
	excerpt,
	author,
	isExternal,
	containImgWidth,
	maxCol
}) => {
	const ExternalLink = StyledLink.withComponent('a'),
		LinkComponent = isExternal ? ExternalLink : StyledLink,
		isSvg = cover.extension === 'svg';
	return (
		<Wrapper>
			{isSvg ? (
				<img
					src={cover.publicURL}
					style={imageStyles({ theme, containImgWidth })}
					alt="project logo"
				/>
			) : (
				<Img
					fluid={cover.childImageSharp.fluid}
					imgStyle={imageStyles({ theme, containImgWidth })}
					style={{ position: 'static' }}
				/>
			)}
			<LinkComponent
				href={path}
				to={path}
				target={isExternal ? '_blank' : 'top'}
				rel={isExternal ? 'noopener noreferrer' : ''}
			>
				<Info>
					{date ? <span>{date}</span> : null}
					<Title>{title}</Title>
					{excerpt ? <Excerpt>{excerpt}</Excerpt> : null}
					{author ? <span>by: {author}</span> : null}
				</Info>
			</LinkComponent>
		</Wrapper>
	);
};

export default PostListItem;

PostListItem.propTypes = {
	cover: PropTypes.object.isRequired,
	path: PropTypes.string.isRequired,
	excerpt: PropTypes.string,
	date: PropTypes.string,
	title: PropTypes.string.isRequired,
	author: PropTypes.string,
	isExternal: PropTypes.bool,
	containImgWidth: PropTypes.bool,
	maxCol: PropTypes.number,
};

export const query = graphql`
	fragment PostListImageFluid on File {
		childImageSharp {
			fluid(maxWidth: 1000, quality: 90, traceSVG: { color: "#2B2B2F" }) {
				...GatsbyImageSharpFluid_withWebp_tracedSVG
			}
		}
	}
`;
